.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 0.8/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
    transition: var(--transition);
}

.about__me {
    width: 100%;
    aspect-ratio: 0.8/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
    transition: var(--transition);
}

#about img {
    margin-bottom: 10px;
    border-top-left-radius: 20%;
    border-bottom-right-radius: 20%;
    box-shadow: 7px 1px 1px #5B4B8A, 10px 10px 0 -5px #eee, 15px 10px 1px -4px #4C3575,
        20px 20px 0 -10px #eee, 0 20px 1px -9px #333;
}



.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
}

.about__me-image:hover {
    transform: rotate(0);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: var(--dark);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
}

.about__card:hover {
    background: transparent;
    border-color: var(--light);
    cursor: default;
}

.about__icon {
    color: var(--white);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--lightest);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--white);
}

.lighten {
    color: var(--white);
}

/* ==================== Media queries ===================== */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__contenet p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}